#offer {
    height: 700px;
    background-image: url("../images/paintstrokes.jpg");
    background-size: cover;
    background-attachment: fixed;
    color: $colorText2;
}

#offer .container {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

#offer .offerList {
    height: 90%;
    display: flex;
    justify-content: space-between;
}

#offer .offerList .offerItem {
    height: 100%;
    width: 31%;
    background: rgba($colorLight2, 0.9);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
}

#offer .offerList .offerItem .offerInfo {
    height: 50%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    padding: 0 10px;
}

#offer .offerList .offerInfoTitleHolder {
    height: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

#offer .offerList .offerInfoTitleHolder .offerInfoTitle {
    font-size: 40px;
    font-weight: lighter;
    text-align: center;
}

#offer .offerList .offerInfoText {
    height: 55%;
    font-weight: lighter;
    text-align: justify;
}

#offer .offerList .offerItem .offerInfoMore {
    height: 50%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    padding: 0 10px;
}

#offer .offerList .offerInfoImgHolder {
    margin-top: 10px;
    height: 70%;
    width: 95%;;
    display: flex;
    justify-content: center;
    align-items: flex-end;
}

#offer .offerList .offerInfoImg {
    max-width: 100%;
    max-height: 100%;
}

#offer .offerList button {
    font-family: 'Nunito', sans-serif;
    width: 240px;
    height: 50px;
    margin: 20px 0 10px 0;
    background: $colorSecondary2;
    font-size: 20px;
    border: 0;
    border-radius: 5px;
    color: #FFFFFF;
    cursor: pointer;
}

#offer .offerList button:hover {
    background: $colorPrimary1;
    color: $colorText1;
}

@media (max-width: 1050px) {

    #offer {
        height: 1100px;
    }

    #offer .offerList {
        height: 95%;
        flex-wrap: wrap;
        justify-content: center;
    }

    #offer .offerList .offerItem {
        height: 32%;
        width: 99%;
        flex-direction: row;
    }

    #offer .offerList .offerItem .offerInfo {
        height: 95%;
        width: 50%;
        justify-content: center;
    }

    #offer .offerList .offerItem .offerInfoTitleHolder {
        padding-top: 10px;
        height: 40%;
    }

    #offer .offerList .offerItem .offerInfoText {
        padding-top: 10px;
    }

    #offer .offerList .offerItem .offerInfoMore {
        height: 95%;
        width: 50%;
    }

    #offer .offerList .offerItem .offerInfoMore .offerInfoImgHolder {
        margin-top: 0;
        align-items: center;
        height: 90%;
    }

    #offer .offerList .offerItem .offerInfoMore button {
        margin-top: 10px;
    }
}